import axios from 'axios'
import ImageCarousel from '../components/ImageCarousel'


async function getProduct(slug) {
  const BASE_URL = 'http://localhost:1337'
  const res = await axios.get(`${BASE_URL}/api/products?filters[slug][$eq]=${slug}&populate=*`)
  return res.data.data[0]
}

export default async function ProductDetail({ params }) {
  const slug = params?.slug
  const product = await getProduct(slug)

  const images = product?.images || []

  return (
    <div className="p-4 max-w-4xl mx-auto">
      <ImageCarousel images={images} />
      {/* <h1 className="text-2xl font-bold mt-4">{product.title}</h1> */}
      <p className="text-gray-600 line-through">Rs. {product.oldPrice}</p>
      <p className="text-red-600 font-bold text-xl">Rs. {product.price}</p>
      <p className="mt-4 text-gray-700 whitespace-pre-line">{product.description}</p>
    </div>
  )
}
